import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { DeviceService } from 'src/app/core/services/device.service';
import { GtmService } from 'src/app/core/services/gtm.service';
import { SeoService } from 'src/app/core/services/header/seo.service';
import { HomeService } from 'src/app/core/services/home.service';
import { IBreadcrumb } from 'src/app/shared/breadcrumb/breadcrumb.component';

@Component({
  selector: 'app-promocoes',
  templateUrl: './promocoes.component.html',
  styleUrls: ['./promocoes.component.scss']
})
export class PromocoesComponent implements OnInit {

  public isMobile: boolean;
  public screenTypeBySize: string;

  public promocoes: any = []

  public steps: IBreadcrumb[] = [
    {
      id: '0',
      label: 'Voltar para home',
      link: '/'
    },
    {
      id: '1',
      label: 'Promoções'
    }
  ];

  constructor(
    private titleS: Title,
    private device: DeviceService,
    private router: Router,
    private gtm: GtmService,
    private homeService: HomeService,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.titleS.setTitle('Zero Km - Promoções');
    this.seoService.createLinkForCanonicalURL();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - Promoções');
    this.isMobile = this.device.isMobile();
    this.screenTypeBySize = this.device.screenTypeBySize();
    this.loadPromotions();
  }

  getImageType() {
    return this.screenTypeBySize + '.png';
  }

  redirectTo(route) {
    this.router.navigate([`${route}`]);
  }

  private loadPromotions(): void {

    this.homeService.getPromocoes().subscribe((response: any) => {
      this.promocoes = response.data.filter((promocao: any) => {
        return promocao.active == true;
      });
    }, error => console.log(error));
  }
}
