import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { throwError as observableThrowError } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { API_BE_V3 } from 'src/app/core/api/app.api';

@Injectable({
    providedIn: 'root'
})
export class HomeService {

    constructor(private http: HttpClient) { }

    getBanners() {
        const env = environment.production ? 'prod' : 'dev';
        return this.http.get<any>(`${API_BE_V3}/banners?env=${env}`).pipe(tap(data => catchError(this.errorHandler)));
    }

    getDatasheets() {
        const env = environment.production ? 'prod' : 'dev';
        return this.http.get<any>(`${API_BE_V3}/datasheets?env=${env}`).pipe(tap(data => catchError(this.errorHandler)));
    }

    getPromocoes() {
        const env = environment.production ? 'prod' : 'dev';
        return this.http.get<any>(`${API_BE_V3}/promocoes?env=${env}`).pipe(tap(data => catchError(this.errorHandler)));
    }

    getMenu() {
        const env = environment.production ? 'prod' : 'dev';
        return this.http.get<any>(`${API_BE_V3}/menu?env=${env}`).pipe(tap(data => catchError(this.errorHandler)));
    }

    private errorHandler(error: HttpErrorResponse) {
        return observableThrowError(error.message || "Server Error");
    }
}
